import axios from 'axios';
import { Module } from 'vuex';
import {
  RootState, OnlineTestState, ISubjectCourseOrigin, IAmountListOrigin, IScopeListOrigin,
} from './types';

const onlineTest: Module<OnlineTestState, RootState> = {
  namespaced: true,
  state: {
    subjectCourseList: [],
    amountList: [],
    scopeList: [],
  },
  mutations: {
    SAVE_SUBJECT_COURSE_LIST(state, list) {
      state.subjectCourseList = list.map((d: ISubjectCourseOrigin) => ({
        id: d.rid,
        title: d.Title,
      }));
    },
    SAVE_AMOUNT_LIST(state, list) {
      state.amountList = list.map((d: IAmountListOrigin) => ({
        id: d.value,
        title: d.name,
      }));
    },
    SAVE_SCOPE_LIST(state, list) {
      state.scopeList = list.map((d: IScopeListOrigin) => ({
        id: d.value,
        title: d.name,
      }));
    },
  },
  actions: {
    getSubjectCourseList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Get_SubjectCourse_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_SUBJECT_COURSE_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getAmountList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Get_Amount_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_AMOUNT_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getScopeList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Get_Scope_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_SCOPE_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    postCreateTestList({ commit, rootState }, params) {
      const temp = {
        ...params,
        // eslint-disable-next-line
        account_id: rootState.memberId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Post_Create_Test_List', temp)
          .then((response) => {
            const { data } = response;
            console.log(data);
            // if (data.Code === '0000') commit('SAVE_SCOPE_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {

  },
};

export default onlineTest;
