import axios from 'axios';
import { Module } from 'vuex';
import {
  RootState, TagsState, ITagsOrigin,
} from './types';

const tags: Module<TagsState, RootState> = {
  namespaced: true,
  state: {
    tags: [],
    selectedTags: [],
  },
  mutations: {
    SAVE_TAGS(state, data) {
      state.tags = data.map((d: ITagsOrigin) => ({
        text: d.Text,
        value: d.Value,
      }));
    },
    SAVE_USER_TAGS(state, data) {
      state.selectedTags = data.map((d: ITagsOrigin) => ({
        text: d.Text,
        value: d.Value,
      }));
    },
  },
  actions: {
    getAllTag({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/GetAllTag')
          .then((response) => {
            const { data } = response;
            commit('SAVE_TAGS', data.Data);
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    addTags({ rootState }, list: string[]) {
      const temp = {
        UserGuid: rootState.userId,
        Tag: list.join(','),
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/AddTag', temp)
          .then((response) => {
            const { data } = response;
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getTag({ commit, rootState }) {
      const temp = {
        UserGuid: rootState.userId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/GetTag', temp)
          .then((response) => {
            const { data } = response;
            if (data.Data !== null) commit('SAVE_USER_TAGS', data.Data);
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {

  },
};

export default tags;
