import axios from 'axios';
import { Module } from 'vuex';
import {
  RootState, TestState, ITestItemOrigin, ITestTemplateOrigin,
} from './types';

const pastTest: Module<TestState, RootState> = {
  namespaced: true,
  state: {
    yearList: [],
    typeList: [],
    levelList: [],
    subjectList: [],
    courseList: [],
    historyExamList: [],
    errorQuz: '',
  },
  mutations: {
    SAVE_YEAR_LIST(state, list) {
      state.yearList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      }));
    },
    SAVE_TYPE_LIST(state, list) {
      state.typeList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      }));
    },
    SAVE_LEVEL_LIST(state, list) {
      state.levelList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      }));
    },
    SAVE_SUBJECT_LIST(state, list) {
      state.subjectList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      }));
    },
    SAVE_COURSE_LIST(state, list) {
      state.courseList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      }));
    },
    SAVE_HISTORY_EXAM_LIST(state, list) {
      state.historyExamList = list.map((d: ITestTemplateOrigin) => ({
        id: d.rid,
        class: d.Class,
        years: d.Years,
        degree: d.Degree,
        name: d.Name,
        subject: d.Subject,
        flagQ: d.flagQ,
        flagA: d.flagA,
      }));
    },
    SAVE_ERROR_QUESTION(state, quz) {
      state.errorQuz = quz;
    },
  },
  actions: {
    getYearList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetYear_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_YEAR_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getTypeList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetType_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getLevelList({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetLevel_List', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_LEVEL_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getSubjectList({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetSubject_List', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_SUBJECT_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getCourseList({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetCourse_List', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_COURSE_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getHistoryExamList({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetHistoryExam_List', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_HISTORY_EXAM_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    saveErrorQuestion({ commit }, quz) {
      commit('SAVE_ERROR_QUESTION', quz);
    },
  },
  getters: {

  },
};

export default pastTest;
